body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.rating .list {
  overflow: hidden;
  margin: 15px auto;
  width: 80%;
}

.rating .tip {
  margin: 40px 0 25px;
  font-weight: bold;
  font-size: 16px;
}

.rating .btn {
  margin-top: 60px;
}

.rating .change_btn {
  margin-left: 15px;
}
.loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  text-align: center;
  background: rgba(255,255,255,0.1);
  z-index: 10;
}

.loading .loading_icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 11;
}
.movie_item {
  float: left;
  display: -webkit-flex;
  display: flex;
  width: 47%;
  margin: 15px;
  border: 1px solid #efefef;
}

.movie_item .item_left,.item_right {
  padding: 15px;
}

.movie_item .item_left img {
  width: 185px;
  height: 278px;
}

.movie_item .item_right {
  padding-left: 0;
  width: 100%;
}

.movie_item .overview {
  height: 160px;
  font-size: 12px;
  overflow: hidden;
}

.movie_item .title {
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
}

.movie_item .rate .number {
  display: inline-block;
  margin-left: 10px;
  font-weight: bold;
}

.movie_item .genres {
  font-size: 12px;
  margin: 8px 0;
}
.result .list {
  overflow: hidden;
  margin: 15px auto;
  width: 80%;
}

.result .tip {
  margin: 40px 0 25px;
  font-weight: bold;
  font-size: 16px;
}
.welcome {
  padding-top: 150px;
  font-size: 48px;
  font-weight: bold;
}

.welcome .link {
  font-size: 34px;
  margin-top: 50px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

